<template>
  <div id="main">
    <el-tabs type="border-card">
      <el-tab-pane :label="item.name" v-for="(item,index) in listName" :key="index">
        <div class="main_div" id="items">
          <!-- @dragstart.capture="drag(it)" -->
          
          <div
            class="main_div_k"
            v-for="(it,ind) in item.list"
            v-show="cpmpDataAttribute[it.key].authFrom?authFrom==cpmpDataAttribute[it.key].authFrom:true "
            :key="ind"
            @click="pushState(it)"
            :data-ind="ind"
            :data-index="index"
          >
            <label>
              <button class="buttom">
                {{cpmpDataAttribute[it.key].name}}
                <component
                  class="my-swiper none"
                  :is="it.key"
                  indicator-color="white"
                  :ref="it.key+it.typeId+ind"
                  :dataList="it.data"
                  :setAttribute="it.setAttribute"
                  :childs="it.childs"
                >{{it.text}}</component>
              </button>
            </label>

            <!-- <p class="left_t_text">{{it.name}}</p> -->
          </div>
        </div>
      </el-tab-pane>
    </el-tabs>
  </div>
</template>

<script>
import Sortable from "sortablejs";
import cpmpDataAttribute from './configuration/cpmpDataAttribute'
// import management from './manageConfig'
import management from "./configuration/management";
// let dataJson= management.cpmpDataJson
// let compList= management.cpmpList

// console.log("management",management);
export default {
  name: "compLeft",
  components: management.cpmpList,
  data() {
    return {
      authFrom:2,
      cpmpDataAttribute,
      listName: [
        {
          name: "基础组件",
          list: management.cpmpDataJson,
        },
        // ,{ name: "标准组件" },
      ],
      active: 0,
      a: "tabbar",
      it: {},
      none: true,
    };
  },
  watch: {
    ["$store.state.type.componentList"](value) {
      this.indexData = value.length;
      // this.listName[0].list= value.setAttribute
    },
    ["$store.state.authFrom.authFromInfo.authFrom"](value) {
       this.authFrom = value
     },
  },
  methods: {
    // 点击
    pushState(it) {
     
      it = JSON.parse(JSON.stringify(it));
     
      this.$store.dispatch("type/pushTypeListActions", it);
    },
    //  拖拽
    // drag(it) {
    //   it = JSON.parse(JSON.stringify(it));
    //   this.it = it;
    // },
    setArr(event, item) {
      event || item;
    },
    test() {
      var that = this;
      var el = document.getElementById("items");
      // 常用
      new Sortable(el, {
        group: {
          name: "shared",
          pull: "clone", // To clone: set pull to 'clone'
          put: false,
        }, //需要相同或的group值
        sort: false,
        animation: 150, //移动动画
        onChoose(evt) {
          let index = evt.item.dataset.index;
          let ind = evt.item.dataset.ind;
          let it = JSON.stringify(that.listName[index].list[ind]);
          that.$store.dispatch("type/addTypeActions", {
            itemDataLeft: JSON.parse(it),
          });
        },
/*         onEnd(evt) {
          console.log("end", evt);
        }, */
      });
    },
  },
  mounted() {
    this.test();
  },
  
};
</script>

<style>
* {
  margin: 0;
  padding: 0;
}
.buttom {
  border: 1px solid #dedede;
  background: white;
  padding: 5px 10px;
  margin: 5px 5px;
}
.none {
  display: none;
}

.main_div_k .van-tabbar--fixed {
  position: relative !important;
}
.my-Swipe .van-swipe-item {
  color: #fff;
  font-size: 20px;
  line-height: 150px;
  text-align: center;
  background-color: #39a9ed;
}
.left_t_text {
  font-size: 11px;
  margin: 10px 0 0 0;
}
.main_div_k {
  margin-bottom: 10px;
  border: none !important;
}
.main_div {
  padding-top: 10px;
  display: flex;
  flex-flow: row wrap;
  justify-content: flex-start;
  align-items: center;
  overflow: auto;
}
.el-tabs__content {
  min-height: calc(100vh - 116px);
  padding-right: 0 !important;
  padding-bottom: 0 !important;
}

.el-tabs__nav is-top {
  height: auto;
}
.item {
  width: 100px;
  height: 100px;
  border: solid 1px #ccc;
}
.flex {
  display: flex;
  justify-content: space-around;
}
.el-header,
.el-footer {
  background-color: #b3c0d1;
  color: #333;
  text-align: center;
  line-height: 60px;
}

.el-aside {
  background-color: #d3dce6;
  color: #333;
  text-align: center;
}

.el-main {
  background-color: #e9eef3;
  color: #333;
  text-align: center;
}

body > .el-container {
  margin-bottom: 40px;
}

.el-container:nth-child(5) .el-aside,
.el-container:nth-child(6) .el-aside {
  line-height: 260px;
}

.el-container:nth-child(7) .el-aside {
  line-height: 320px;
}
</style>
