<template>
  <div id="main" class="main">
    <div v-if="mainState" class="main_state">
      <div class="header">
        <div class="bai_kuang"></div>
        <img src="../assets/hear1.png" alt />
        <div class="maix_text_hande">
          {{authFromNameData.authFromData[authFromData.authFrom]}}-{{authFromNameData.pageList[authFromData.componentIndex]}}（
          <strong
            :style="authFromData.isDraftData?'color: rgba(244, 65, 65, 1);':''"
          >{{authFromData.isDraftData==1?"草稿":"正式"}}</strong>）
        </div>
      </div>
      <div class="flexs">
        <div id="items1" class="items_class">
          <!-- 其余部分 -->
          <div
            :class="judgeKey(it.key)?'ignore-elements':''"
            @click="setState(it,ind)"
            v-for="(it,ind) in componentList"
            :key="it.key+it.typeId+ind"
          >
            <el-tooltip
              effect="dark"
              :content="cpmpDataAttribute[it.key].name"
              placement="right-start"
            >
              <!-- <div v-show="hoverSate == ind" class="hoverSate">{{it.name}}</div> -->
              <component
                class="my-swipe"
                :is="it.key"
                indicator-color="white"
                :ref="it.key+it.typeId+ind"
                :dataList="it.data"
                :setAttribute="it.setAttribute"
                :childs="it.childs"
                :stylesObj="it.stylesObj"
              ></component>
            </el-tooltip>
          </div>
          <!-- <div class="dibu_kaui ignore-elements"></div> -->
        </div>
      </div>
    </div>
    <!-- 其余部分 -->
    <div @click="setTabbarState()" class="tabbar" v-show="authFromData.componentIndex==0">
      <el-tooltip effect="dark" content="底部导航栏" placement="right-start">
        <!-- <div v-show="hoverSate == ind" class="hoverSate">{{it.name}}</div> -->
        <component
          :is="'tabbar'"
          indicator-color="white"
          :dataList="tabbarData.data"
          :childs="tabbarData.childs"
        ></component>
      </el-tooltip>
      <!-- {{tabbarData.data}} -->
    </div>
  </div>
</template>

<script>
import Sortable from "sortablejs";
import cpmpDataAttribute from "./configuration/cpmpDataAttribute";
// import management from './manageConfig'
import management from "./configuration/management";
import tabbarLset from "./configuration/tabbar";
import { narData } from "../config";
import { keyValueControl } from "../config";
const { topKey, bottomKey } = keyValueControl; //判断作用  在此数组内的 组件不限制添加个数
const tabbarData = JSON.parse(JSON.stringify(tabbarLset));

// const compList = management.cpmpList
// console.log("compList",compList);
export default {
  name: "compMain",

  data() {
    return {
      cpmpDataAttribute,
      tabbarData: tabbarData,
      mainState: true,
      value: [],
      componentList: [],
      itemDataLeft: {},
      hoverSate: false,
      hoverName: "",
      mainList: [],
      authFromData: {
        authFrom: 2, //小程序类型
        isDraft: 1, //获取类型
        componentIndex: 0, //页面索引
        isDraftData: 1,
      },
      authFromNameData: {
        authFromData: {},
        pageList: {},
      },
    };
  },
  watch: {
    ["$store.state.authFrom.authFromInfo"](value) {
      this.authFromData = { ...value };
      
      this.getJsonData({ ...value });
    },

    /*["$store.state.authFrom.isDraft"](value) {
      this.authFromData.isDraft = value;
    },
    ["$store.state.authFrom.componentIndex"](value) {
      this.authFromData.componentIndex = value;
      let authFrom = this.authFromData.authFrom;
      this.getJsonData({ authFrom, componentIndex: value });
    },
    ["$store.state.authFrom.isDraftData"](value) {
      this.authFromData.isDraftData = value;
      // let { authFrom, componentIndex } = this.authFromData;
      // this.getJsonData({ authFrom, componentIndex});
    }, */
    // 底部导航条
    ["$store.state.type.tabbarData"](value) {
      this.tabbarData = value;
    },
    ["$store.state.type.componentList"](value) {
      // this.mainList=value.filter(item=>{
      //   return item.name=="搜索"||item.name=="轮播图"||item.name=="宫格图"
      // })
      // this.componentList=value.filter(item=>{
      //   return item.name!="搜索"&&item.name!="轮播图"&&item.name!="宫格图"
      // })
      // console.log("主要",this.mainList,this.componentList)
      this.componentList = value;
      this.value = value;
    },
    /*  ["$store.state.type.itemDataLeft"](value) {
      this.itemDataLeft = value;
    }, */
  },
  components: management.cpmpList,
  methods: {
    /* hover(name,ind){
      this.hoverSate=ind,
      this.hoverName=name
    }, */

    setTabbarState() {
      let it = this.tabbarData;

      this.$store.dispatch("type/setTypeActions", { it, ind: -1 });
    },
    setState(it, ind) {
      this.$store.dispatch("type/setTypeActions", { it, ind });
    },
    // 控制是否可以拖动
    judgeKey(key) {
      return topKey.lastIndexOf(key) != -1 || bottomKey.lastIndexOf(key) != -1;
    },
    test() {
      var that = this;
      var el = document.getElementById("items1");
      // 常用
      new Sortable(el, {
        group: {
          name: "shared",
          pull: "clone", // To clone: set pull to 'clone'
          put: true,
        }, //需要相同或的group值
        filter: ".ignore-elements",
        animation: 150, //移动动画
        onAdd(evt) {
          evt.item.remove();
          that.$store.dispatch("type/addSetTypeActions", {
            newIndex: evt.newIndex,
          });
        },
        onEnd: function (evt) {
          //  组件被拖动之后
          if (topKey.lastIndexOf(that.value[evt.newIndex].key) != -1) {
            that.$message({
              message: "警告哦，不可移动到头部组件上方哦",
              type: "warning",
            });
            // that.$forceUpdate()
            that.mainState = false;
            setTimeout(() => {
              that.mainState = true;
              setTimeout(() => {
                that.test();
              });
            }, 10);
            return;
          }
          if (bottomKey.lastIndexOf(that.value[evt.newIndex].key) != -1) {
            that.$message({
              message: "警告哦，不可移动到底部组件下方哦",
              type: "warning",
            });
            // that.$forceUpdate()
            that.mainState = false;
            setTimeout(() => {
              that.mainState = true;
              setTimeout(() => {
                that.test();
              });
            }, 10);
            return;
          }
          // let newArray = [];
          if (that.value.length) {
            // 获取排序之后的data数据
            that.value.splice(
              evt.newIndex,
              0,
              that.value.splice(evt.oldIndex, 1)[0]
            );
          } else {
            that.value.splice(that.value.splice(evt.oldIndex, 1)[0]);
          }
          that.value = JSON.parse(JSON.stringify(that.value));

          that.$store.dispatch("type/updataTypeListActions", that.value);
        },
      });
    },
    // 获取json数据
    getJsonData(data = { authFrom: 2, componentIndex: "0", isDraftData: 1 }) {
      let { authFrom, componentIndex, isDraftData } = data;
      console.log("dadas",data);
      this.$api
        .getJsonData({
          componentIndex: componentIndex || "0",
          authFrom: authFrom || 2,
          isDraft: isDraftData,
        })
        .then((res) => {
          let value = res?.value ?? "[]";
          let JSONdata = JSON.parse(value);
          this.$store.dispatch("type/updataTypeListActions", JSONdata);
          if (componentIndex != 0) {
            return;
          }
          console.log("232",authFrom);
          this.$api
            .getJsonData({
              componentIndex: "-1",
              authFrom: authFrom || 2,
              isDraft: isDraftData,
            })
            .then((res) => {
              let value = res.value;
              let JSONdata = JSON.parse(value);
              if (value == "{}" || !value) {
                return;
              }
              this.$store.dispatch("type/updataTabbarDataActions", {
                itemData: JSONdata,
              });
            });
        });
    },
  },
  mounted() {
    let authFromData = {};
    let pageList = {};
    this.$store.state.authFrom.authFromData.forEach((item) => {
      authFromData[item.authFrom] = item.name;
    });

    narData.pageList.forEach((item) => {
      pageList[item.label] = item.name;
    });
    this.authFromNameData = {
      authFromData,
      pageList,
    };

    this.test();
    let {authFrom}=this.$store.state.authFrom.authFromData[0]??''
    this.getJsonData({ authFrom, componentIndex: "0" });
    /*  this.$api
      .getJsonData({
        componentIndex: "0",
        authFrom: 2,
        isDraft: 1,
      })
      .then((res) => {
        let value = res.value;
        let JSONdata = JSON.parse(value);
        console.log(JSONdata);
        this.$store.dispatch("type/updataTypeListActions", JSONdata);
        this.getJsonData();
      }); */

    /* let JSONdata = window.localStorage.getItem("JSONdata");
    console.log("tabbarData",tabbarData);
    this.$store.dispatch("type/updataTabbarDataActions", {
      itemData:tabbarData
    });
    if (JSONdata) {
      JSONdata = JSON.parse(JSONdata);
      this.$store.dispatch(
        "type/updataTypeListActions",
        JSONdata.componentList
      );
      this.$store.dispatch("type/updataTabbarDataActions", {
        itemData: JSONdata.tabbarData,
      });
      return;
    } */
  },
};
</script>

<style>
.bai_kuang {
  width: 100%;
  height: 87px;
  margin-bottom: -74px;
  background: #fff;
}
.main_state {
  position: relative;
  padding-top: 84px;
}
.main {
  position: relative;
  height: 100%;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}
.maix_text_hande {
  position: absolute;
  top: 60px;
  left: 14px;
  font-weight: bold;
}
.tabbar {
  position: absolute;
  bottom: 0px;
}
.item {
  width: 100px;
  height: 100px;
  border: solid 1px #ccc;
}

.items_class {
  height: calc(100% - 90px);
}
.van-tabbar--fixed {
  position: absolute !important;
}
.header {
  position: absolute;
  top: 0;
  width: 100%;
  z-index: 999;
  /* margin-bottom: -88px; */
}
.header img {
  margin-top: -10px;
  width: 100%;
}

.flexs {
  width: 375px;
  height: calc(100vh - 300px);
  position: relative;
  background: #eff0f2;
  overflow-y: scroll;
  /* padding-bottom: 40px; */
}
.dibu_kaui {
  width: 100%;
  height: 40px;
}
.flexs::-webkit-scrollbar {
  /* background: #b3c0d1; */
  background: #eff0f2;
  width: 6px;
}
.flexs::-webkit-scrollbar-thumb {
  box-sizing: border-box;
  border-radius: 10px;
  -webkit-box-shadow: inset 0 0 5px rgba(0, 0, 0, 0.2);
  background: rgba(0, 0, 0, 0.2);
}
</style>
