<template>
  <div class="slider_my">
    {{itemc||'0px'}}
    <el-slider v-model="value" @input="input" :format-tooltip="formatTooltip"></el-slider>
  </div>
</template>

<script>
export default {
  name: "sliderMy",
  data() {
    return {
      value: 0,
      inputState: true,
    };
  },
  props: {
    itemc: {
      // 当前的数值
      type: String,
      default: "",
    },
    it: {
      //
      type: Object,
      default: () => {},
    },
    indexc: {
      type: Number,
      default: -1,
    },
    childsCode: {
      type: String,
      default: "",
    },
  },
  watch: {

  
    itemc(val) {
      val = val.replace(/px/gi, "");
      this.value = Number(val) - Number(this.it.remark);
      // console.log("this.value", Number(val));
    },
  },
  methods: {
    formatTooltip(val) {
      // 处理格式化
      return Number(val) + Number(this.it.remark) + "px";
    },
    input(val) {
      if (this.inputState) {
        //
        this.inputState = false;
        return;
      }
      // console.log("input", val);
      let remark = this.it.remark || 0;
      // 向父元素传递信息
      this.$emit(
        "setJumpUrl",
        this.indexc,
        Number(val) + Number(remark) + "px",
        this.it.valueCode,
        this.childsCode
      );
    },
  },
  created() {
    let val = this.itemc.replace(/px/gi, "");
    this.value = Number(val) - Number(this.it.remark);
  },
};
</script>
<style lang="css">
.slider_my{
  margin-right: 10px;
}
</style>