 <template>
  <div>
    <el-select
      v-model="itemc[it.valueCode]"
      placeholder="请选择"
      @change="changeUrl(itemc[it.valueCode],itemc)"
    >
      <el-option
        v-for="(item,index) in jumpUrlConfigapi"
        :label="item.name"
        :value="item.value"
        :key="index+'index02'"
      ></el-option>
    </el-select>
    <template v-if="this.urlState?itemc.urlState:false">
      <el-select
        v-model="jumpUrlData"
        @change="changeUrl2(itemc,jumpUrlData)"
        filterable
        remote
        reserve-keyword
        :placeholder="'请输入'+(itemc.urlState.placeholder||'关键词')"
        :remote-method="remoteMethod"
        :loading="loading"
      >
        <el-option
          v-for="(itUrl,indUrl) in optionsUrl"
          :key="'value'+indUrl"
          :label="itUrl[itemc.urlState.queryValue]"
          :value="itUrl"
        ></el-option>
      </el-select>
    </template>
    <br />当前跳转地址：
    <br />
    {{itemc.jumpUrl}}
    <br />
    <div v-if="itemc.nameVal">当前跳转名称：{{itemc.nameVal}}</div>
  </div>
</template>
<script>
import axios from "../axios/axios";
import { jumpUrlConfigapi } from "../config";
export default {
  name: "selects",
  data() {
    return {
      optionsUrl: [],
      loading: false,
      urlState: true,
      jumpUrlData: {},
      jumpUrlConfigapi,
    };
  },
  props: {
    itemc: {
      type: Object,
      default: () => {},
    },
    it: {
      type: Object,
      default: () => {},
    },
    indexc: {
      type: Number,
      default: -1,
    },
    childsCode: {
      type: String,
      default: "",
    },
  },
  watch: {
    ["$store.state.authFrom.jumpUrlConfigapi"](val) {
      if(!val) return
      this.jumpUrlConfigapi = val;
    },
  },
  methods: {
    // 第一级被选中时的回调函数
    changeUrl(val, it) {
      let changeUrl = this.jumpUrlConfigapi;
      let urlState = changeUrl.filter((items) => items.value === val);
      if (urlState[0].api) {
        it.urlState = urlState[0];
        this.urlState = urlState[0];
      } else {
        this.urlState = false;
        it.urlState = false;
        it.jumpUrl = val;
        this.$emit("setJumpUrl", this.indexc, it, this.childsCode);
      }
    },
    // 第二级被选中时的回调函数
    changeUrl2(it, val) {
      let query = "?";

      it.urlState.query.forEach((item) => {
        if (item instanceof Array) {
          query += `${item[1]}=${val[item[0]] || ""}&`;
          return;
        }
        query += `${item}=${val[item] || ""}&`;
      });
      it.jumpUrl = it.urlState.value + query;
      it.nameVal = val[it.urlState.queryValue];
      this.$emit("setJumpUrl", this.indexc, it, this.childsCode);
    },

    // 处理地址搜索项
    remoteMethod(query) {
      let that = this;
      if (query !== "") {
        this.loading = true;
        let api = this.itemc.urlState.api;
        let queryValue = this.itemc.urlState.queryValue;
        let industryType = this.$store.state.authFrom.authFromInfo.authFrom
        axios
          .get(api, {
            params: {
              [queryValue]: query,
              industryType
            },
          })
          .then((res) => {
            that.optionsUrl = res;
            that.loading = false;
          })
          .catch((err) => {
            console.log("err", err);
            that.optionsUrl = [
              //测试数据
              {
                actualSaleNum: 0,
                barCode: null,
                brand: "",
                browseNumber: 0,
                buyingPrice: 0,
                categoryId: 1085,
                categoryName: "优选套餐",
                cellingPrice: 1,
                createTime: "2020-09-04 10:33:49",
                evaluationNumber: 0,
                expiration: null,
                goodsDesc: "",
                goodsId: 100112,
                goodsModule: "0",
                goodsName: "养生黑米粥+蒸饺+腐乳",
                goodsPropsType: 0,
                goodsTag: "",
                goodsType: 2,
                graphicDesc: "",
                imgPathMain:
                  "https://xxpayvip.oss-cn-beijing.aliyuncs.com/goods/222b18a5-4a44-483d-9183-f3618e2ff742.jpg",
                imgPathMore: "",
                industryType: 1,
                isRecommend: 0,
                isvId: 40000006,
                mchId: 20000076,
                memberPrice: 1,
                miniAuditId: null,
                miniGoodsId: null,
                producedBeginTime: null,
                ps: null,
                recommendSort: 1000000,
                status: 1,
                stockLimitType: 1,
                stockNum: null,
                storeLimitType: 0,
                subGoodsId: "100111,100108,100106",
                supplier: "",
                unit: "",
                updateTime: "2020-09-04 10:39:30",
                virtualSaleNum: null,
              },
            ];
            console.log("err", that.options);
            that.loading = false;
          });
      } else {
        this.options = [];
      }
    },
  },
  created() {
    this.jumpUrlConfigapi= this.$store.state.authFrom.jumpUrlConfigapi;
  },
};
</script>
<style lang="css">
</style>