/**
 *  
 */
import { v4 as uuidv4 } from 'uuid';
import Vue from 'vue';
import Vuex from "vuex";
import cpmpDataAttribute from "../components/configuration/cpmpDataAttribute";
import { keyValueControl } from '../config'
Vue.use(Vuex);
const keyVal = keyValueControl.noOnlyKey; //判断作用  在此数组内的 组件不限制添加个数
const topKey = keyValueControl.topKey
const bottomKey = keyValueControl.bottomKey
export default {
    namespaced: true,
    state: {
        tabbarData: {},
        componentList: [// 组件数据

        ],
        itemData: {},//设置项
        indexData: null,//设置项
        itemDataLeft: {},//右侧的拖拽的内容
    },
    mutations: {
        //这里是更新state的数据
        pushTypeList(state, payload) {
            // payload.typeId = (new Date()).valueOf()
            payload.typeId = uuidv4();

            if (keyVal.lastIndexOf(payload.key) == -1) {
                let findIndex = state.componentList.findIndex(item => item.key == payload.key)
                if (findIndex != -1) {
                    window.$message({
                        message: '警告哦，只能添加一个' + cpmpDataAttribute[payload.key].name + '组件',
                        type: 'warning'
                    });
                    return
                }
            }
            /*  window.$notify({
                 title: '成功',
                 message: '成功添加了' + payload.name + '组件',
                 type: 'success'
             }); */


            if (topKey.lastIndexOf(payload.key) != -1) { // 过滤头部组件   
                state.componentList.unshift(payload) // 头部添加到顶部
            } else if (bottomKey.lastIndexOf(payload.key) != -1) { // 过滤商品列表组件   
                state.componentList.push(payload) // 添加在底部
            } else {
                let componentList = JSON.parse(JSON.stringify(state.componentList))
                if (componentList.length) { // 控制组件在始终在头部组件和列表组件之间
                    // let componentListPop = componentList.pop();
                    //  编写一个循环
                    let findXinex = componentList.findIndex(item => bottomKey.lastIndexOf(item.key) != -1)
                    if (findXinex != -1) { // 插入
                        componentList.splice(
                            findXinex,
                            0,
                            payload
                        );
                    } else {
                        componentList.push(payload)
                    }
                    // if (bottomKey.lastIndexOf(payload.key) != -1) {
                    //     componentList.push(componentListPop, payload)
                    // } else {
                    //     componentList.push(payload, componentListPop)
                    // }
                } else {
                    componentList.push(payload)
                }
                state.componentList = componentList
            }
            window.$notify({
                title: '成功',
                message: '成功添加了' + cpmpDataAttribute[payload.key].name + '组件',
                type: 'success',
                position: 'bottom-left'
            });
            // state.componentList.push(payload)
        },
        updateTypeList(state, payload) {
            state.componentList = payload
        },

        updataTypeData(state, payload) {
            let componentList = state.componentList
            componentList[payload.indexData] = payload.itemData
            state.componentList = componentList
        },
        updataTabbarData(state, payload) { // 处理tabbar中的数据
            // console.log("updataTabbarData", payload);
            state.tabbarData = payload.itemData
        },
        addType(state, payload) {
            payload.itemDataLeft.typeId = (new Date()).valueOf()
            state.itemDataLeft = payload.itemDataLeft
        },
        addSetType(state, payload) {
            let itemDataLeft = JSON.parse(JSON.stringify(state.itemDataLeft))
            if (keyVal.lastIndexOf(itemDataLeft.key) == -1) {
                let findIndex = state.componentList.findIndex(item => item.key == itemDataLeft.key)
                if (findIndex != -1) {
                    window.$message({
                        message: '警告哦，只能添加一个' + cpmpDataAttribute[itemDataLeft.key].name + '组件',
                        type: 'warning'
                    });
                    return
                }
            }
            if (topKey.lastIndexOf(itemDataLeft.key) != -1) { // 过滤头部组件   
                state.componentList.unshift(itemDataLeft) // 头部添加到顶部
                window.$message({
                    message: '提示哦，只能将' + cpmpDataAttribute[itemDataLeft.key].name + '组件添加在头部',
                    // type: 'warning'
                });
            } else if (bottomKey.lastIndexOf(itemDataLeft.key) != -1) { // 过滤商品列表组件   
                state.componentList.push(itemDataLeft) // 添加在底部
                window.$message({
                    message: '提示哦，只能将' + cpmpDataAttribute[itemDataLeft.key].name + '组件添加在底部',
                    // type: 'warning'
                });
            } else {
                // let componentList = JSON.parse(JSON.stringify(state.componentList))
                let componentList = state.componentList
                // let newArray = [];
                let findXinex = componentList.findIndex(item => bottomKey.lastIndexOf(item.key) != -1)
                if (findXinex < payload.newIndex && findXinex !== -1) { // 插入
                    componentList.splice(
                        findXinex,
                        0,
                        itemDataLeft
                    );
                    window.$message({
                        message: '提示哦，只能将' + cpmpDataAttribute[itemDataLeft.key].name + '组件添加在底部组件之上',
                        // type: 'warning'
                    });
                    return;
                } else {

                    if (componentList.length) {
                        // 获取排序之后的data数据
                        componentList.splice(
                            payload.newIndex,
                            0,
                            itemDataLeft
                        );
                        // componentList = newArray;
                    } else {
                        componentList.push(itemDataLeft);
                        return;
                    }
                    state.itemDataLeft = componentList
                }
            }



        },

        setType(state, payload) {
            state.itemData = payload.it
            state.indexData = payload.ind
        },
        delTypeData(state, payload) {
            let componentList = state.componentList
            componentList.splice(payload, 1)
            state.componentList = componentList
            if (!componentList[payload]) {
                state.itemData = { ...componentList[payload - 1] }//设置项
                state.indexData = payload - 1//设置项
                return
            }
            state.itemData = { ...componentList[payload] }//设置项
            state.indexData = payload//设置项

        },
    },
    actions: {
        pushTypeListActions(context, type = {}) {
            context.commit('pushTypeList', type)
        },
        updataTypeListActions(context, type = "type_1") {
            context.commit('updateTypeList', type)
        },
        updataTypeDataActions(context, type = {}) {
            context.commit('updataTypeData', type)
        },
        updataTabbarDataActions(context, type = {}) {
            context.commit('updataTabbarData', type)
        },
        addTypeActions(context, type = {}) {
            context.commit('addType', type)
        },
        addSetTypeActions(context, type = {}) {
            context.commit('addSetType', type)
        },
        setTypeActions(context, item = {}) {
            context.commit('setType', item)
        },
        delTypeDataActions(context, indexData = "") {
            context.commit('delTypeData', indexData)
        },
    }

}