/**
 * 封住统一引用规则class 函数
 */

import cpmpDataJson from './cpmpDataJson'
import cpmpList from './cpmpList'

class management {
    static cpmpDataJson = [...cpmpDataJson]
    static cpmpList = { ...cpmpList }
    constructor( data={}) {
        // super();
        console.warn("management",data);
        let {cpmpList={},cpmpDataJson=[]} = data
        if (!cpmpDataJson.length) {
            console.warn("cpmpDataJson为空",data);
            return
        }
        if (!Object.keys(cpmpList).length) {
            console.warn("cpmpList",data);
            return
        }
        this.constructor.cpmpDataJson.push(...cpmpDataJson)
        let that = this
        // Object.assign({ ...that.constructor.cpmpList }, { ...cpmpList })   
        // this.constructor.cpmpList = Object.assign({ ...that.constructor.cpmpList }, { ...cpmpList })   
        for (const key in cpmpList) {
            // if (cpmpList.hasOwnProperty(key)) {
                const element = cpmpList[key];
                that.constructor.cpmpList[key] = element
            // }
        }
    }
}
/**
 *  constructor 
 * 
 * 
 *  */  
export default  management