<template>
  <div class="boDiv">
    <!-- <one></one>
      <two></two>
      <three></three>
    <four></four>-->
    <div class="boss-div" v-for="(x,index) in tabbarFn()" :key="index">
      <div class="boss-div-div" @click="click(index+1)">
        <img :src="x.srcOne" alt v-if="ind!=index+1" />
        <img :src="x.srcTow" alt v-if="ind==index+1" />
      </div>

      <p>{{x.text}}</p>
    </div>
  </div>
</template>
<script>
// import one from "./tabOne"
// import two from "./tabTwo"
// import three from "./tabThree"
// import four from "./tabFour"
import tabbar from "../configuration/tabbar";
const tabbars = JSON.parse(JSON.stringify(tabbar));
export default {
  name: "tabbar",
  // components: {
  //   one,
  //   two,
  //   three,
  //   four
  // },
  props: {
    dataList: {
      type: Object,
      default: () => {},
    },
    stylesObj: {
      type: Object,
      default: () => {},
    },
    setAttribute: {
      type: Array,
      default: () => [],
    },
    childs: {
      type: Array,
      default: () => [],
    },
  },

  data() {
    return {
      active: "01", //
      ind: "01",
      tabbar: {...tabbars},
      authFrom:2
    };
  },
  watch:{
    //监听更改小程序类型
    ["$store.state.authFrom.authFromInfo.authFrom"](val) {
      this.authFrom=val
    },
  },
  methods: {
    tabbarFn() {
      let tabbarChilds = this.tabbar.childs;
      console.log("tabbarChilds", tabbarChilds);
      let childs = this.childs;
      return this.dataList.tabbarType == "0" ? tabbarChilds : childs;
    },
    click(index) {
      this.ind = index;
      console.log(index);
    },
  },
};
</script>
<style>
.boDiv {
  width: 375px;
  padding: 10px 0;
  display: flex;
  flex-flow: row nowrap;
  justify-content: space-around;
  align-items: center;
  background: white;
}
.boss-div-div {
  display: flex;
  flex-flow: row nowrap;
  justify-content: center;
  margin-bottom: 5px;
}
.boss-div-div img {
  width: 30px;
  height: 30px;
}
.boss-div-div p {
  font-size: 18px;
  font-family: PingFang SC, PingFang SC-Medium;
  font-weight: 500;
  text-align: left;
  color: #b3b3b3;
  text-align: center;
}
</style>