<template>
  <div id="index">
    <el-container>
      <el-header>
        <div class="header_div_left">
          <el-radio-group v-model="authFrom" @change="changeAuthFrom">
            <el-radio-button
              :label="item.authFrom"
              v-for="(item, index) in authFromData"
              :key="index"
              >{{ item.name }}</el-radio-button
            >
          </el-radio-group>
        </div>
        <div class="header_div_cet">
          <!-- <el-radio-group v-model="page" @change="changePage"> -->
          <el-radio
            v-model="page"
            @change="changePage"
            border
            :label="item.label"
            v-for="(item, index) in pageList"
            :key="index"
            >{{ item.name }}</el-radio
          >

          <!-- </el-radio-group> -->
        </div>
        <!-- (wxapp/H5)-visual -->
        <div class="header_div">
          <el-link class="link_primary_my" type="primary" @click="zhengshi"
            >拉取{{ isDraftData ? "已发布" : "草稿" }}版本</el-link
          >
          <el-button type="primary" @click="fabu">发布本页</el-button>
          <el-button type="primary" plain @click="baocun">保存草稿</el-button>
        </div>
      </el-header>
      <el-container>
        <el-aside width="400px">
          <compLeft />
        </el-aside>
        <el-container>
          <el-main class="container">
            <compMain />
          </el-main>
        </el-container>
        <el-aside width="400px" class="right">
          <compRight></compRight>
        </el-aside>
      </el-container>
      <!-- <el-footer>Footer</el-footer> -->
      <!--  <el-header>
        <div class="header_div_left">
          <el-radio-group v-model="authFrom" @change="changeAuthFrom">
            <el-radio-button label="1">餐饮</el-radio-button>
            <el-radio-button label="2">商城</el-radio-button>
          </el-radio-group>
        </div>
        <div class="header_div_cet">
          <el-radio v-model="page" @change="changePage" label="1" border>首页</el-radio>
          <el-radio v-model="page" @change="changePage" label="2" border>其他</el-radio>
         
        </div>(wxapp/H5)-visual
        <div class="header_div">
          <el-link class="link_primary_my" type="primary">拉取已发布版本</el-link>
          <el-button type="primary" @click="fabu">发布本页</el-button>
          <el-button type="primary" plain @click="baocun">保存草稿</el-button>
        </div>
      </el-header>-->
    </el-container>
  </div>
</template>

<script>
import compLeft from "./compLeft2";
import compMain from "./compMain4";
import compRight from "./compRight";
import { narData } from "../config";
export default {
  name: "index",

  data() {
    return {
      authFromData: [], //[...narData.authFromData],
      pageList: [...narData.pageList],
      isDraftData: 1,
      authFrom: 2,
      page: 0,
    };
  },

  methods: {
    // 获取正式
    zhengshi() {
      this.$confirm("请注意保存", "提示", {
        confirmButtonText: "获取",
        cancelButtonText: "取消",
        type: "warning",
      })
        .then(() => {
          let { isDraftData } = this.$store.state.authFrom.authFromInfo;
          // this.zhengshiApiFun();
          console.log("isDraftData", isDraftData);
          this.isDraftData = isDraftData ? 0 : 1;
          this.$store.dispatch("authFrom/setTypeDataActions", {
            name: "isDraftData",
            value: isDraftData ? 0 : 1,
          });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "已取消拉取",
          });
        });
    },
    //  请求方法
    zhengshiApiFun() {
      let {
        authFrom,
        componentIndex,
        isDraftData,
      } = this.$store.state.authFrom;
      this.$api
        .getJsonData({
          componentIndex,
          authFrom,
          isDraft: isDraftData ? 0 : 1,
        })
        .then((res) => {
          let value = res?.value ?? "[]";
          let JSONdata = JSON.parse(value);
          this.$store.dispatch("type/updataTypeListActions", JSONdata);
          this.isDraftData = isDraftData ? 0 : 1;
          this.$store.dispatch("authFrom/setTypeDataActions", {
            name: "isDraftData",
            value: isDraftData ? 0 : 1,
          });

          if (componentIndex !== 0) {
            return;
          }
          this.$api
            .getJsonData({
              componentIndex: "-1",
              authFrom,
              isDraft: isDraftData ? 0 : 1,
            })
            .then((res) => {
              if (res.value == "{}") {
                return;
              }
              let value = res.value;
              let JSONdata = JSON.parse(value);
              this.$store.dispatch("type/updataTabbarDataActions", {
                itemData: JSONdata,
              });
            });
        });
    },
    // 更改编辑页面
    changePage(val) {
      console.log("changePage", val);
      // this.page = 0
      if (val == 1) {
        this.$notify.info({
          title: "其他页面正在开发中····",
          message: "请期待！！",
          position: "top-left",
        });
      }
      // 更改传递的页面标识
      this.$store.dispatch("authFrom/setTypePageDataActions", val);
    },
    // 更改编辑的小程序
    changeAuthFrom(val) {
      this.$store.dispatch("authFrom/setTypeDataAutgFromActions", val);
      this.page = 0;
      /*   // 更改页面索引
      this.page = 0 
      this.$store.dispatch("authFrom/setTypeDataActions", {
        name: "componentIndex",
        value: 0,
      });
      //  处理页面为草稿内容
      this.isDraftData = 1
      this.$store.dispatch("authFrom/setTypeDataActions", {
        name: "isDraftData",
        value: 1,
      });
      this.apiServers() */
    },
    // 保存草稿
    async baocun() {
      await this.apiServers("draftJsonData");
      await this.$message({
        message: "恭喜您发布或保存草稿成功！！",
        type: "success",
      });
    },
    //发布
    fabu() {
      this.$confirm("此操作将发布本次编辑, 是否继续?", "提示", {
        confirmButtonText: "发布",
        cancelButtonText: "取消",
        type: "warning",
      })
        .then(async () => {
          await this.apiServers("setJsonData");
          await this.baocun();
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "已取消发布",
          });
        });
    },
    // 公共请求方法
    apiServers(apiFun) {
      let {
        authFrom,
        componentIndex,
      } = this.$store.state.authFrom.authFromInfo; // 获取上传类型
      /* let componentList = JSON.parse(
        JSON.stringify(this.$store.state.type.componentList)
      ); */

      let tabbarData = Object.assign({}, this.$store.state.type.tabbarData);
      tabbarData = JSON.stringify(this.$store.state.type.tabbarData);
      let componentList = Object.assign(
        [],
        this.$store.state.type.componentList
      );
      console.log(JSON.stringify(componentList, null, 4));
      componentList = JSON.stringify(componentList);

      this.$api[apiFun]({
        componentList: encodeURIComponent(componentList),
        // componentList:"[]",
        componentIndex,
        authFrom,
      })
        .then(() => {
          if (componentIndex !== 0 || tabbarData === "{}" || !tabbarData) {
            /* this.$message({
              message: "恭喜您发布或保存草稿成功！！",
              type: "success",
            }); */
            return;
          }
          this.$api[apiFun]({
            // 发布tabbarData配置
            componentList: encodeURIComponent(tabbarData),
            // componentList: "{}",
            componentIndex: -1,
            authFrom,
          }).then(() => {
            /* this.$message({
              message: "恭喜您发布或保存草稿成功！！",
              type: "success",
            }); */
          });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "发布失败",
          });
        });
    },
  },
  components: {
    compLeft,
    compMain,
    compRight,
  },

  created() {
    this.authFromData = this.$store.state.authFrom.authFromData;
    this.authFrom = this.$store.state.authFrom.authFromData[0].authFrom;
    // console.log("ddssd", this.$store.state.authFrom.authFromData);
    this.$store.dispatch("authFrom/setJumpUrlConfigapiActions",this.authFrom); // 获取跳转地址的请求
  },
};
</script>

<style>
html {
  overflow-y: scroll;
}
html::-webkit-scrollbar {
  background: transparent;
  display: none;
}
.header_div_cet {
  /* position: absolute; */
  width: 1400px;
}
.link_primary_my {
  margin-right: 20px;
}
.header_div_left {
  position: absolute;
  left: 20px;
  top: 10px;
}
.right {
  background: #dedede !important ;
}
.el-aside {
  overflow: visible;
}
.container {
  background: #dedede !important;
}
.header_div {
  position: absolute;
  right: 20px;
  top: 0px;
}
.el-header,
.el-footer {
  /* background-color: #b3c0d1; */
  background-color: #fff;
  color: #333;
  text-align: center;
  line-height: 60px;
  position: relative;
  display: flex;
  justify-content: center;
}

.el-aside {
  background-color: #d3dce6;
  color: #333;
  text-align: left !important;
  height: calc(100vh - 60px);
}

.el-main {
  background-color: #e9eef3;
  color: #333;
  text-align: center;
  height: calc(100vh - 60px);
}

body > .el-container {
  margin-bottom: 40px;
}

.el-container:nth-child(5) .el-aside,
.el-container:nth-child(6) .el-aside {
  line-height: 260px;
}

.el-container:nth-child(7) .el-aside {
  line-height: 320px;
}
/* .el-form{
  border:none!important ;
   border-bottom:1px solid #ccc!important ;
} */
</style>
