<template>
  <div>
    <el-upload
      class="upload-demo"
      :action="baseURL + action"
      :multiple="false"
      :headers="getHeaders()"
      :on-preview="handlePreview"
      :on-success="handlesuccess"
      :on-remove="handleRemove"
      :on-error="hadleError"
      :on-progress="hadleProgress"
      :file-list="fileList"
      list-type="picture-card"
      :show-file-list="false"
    >
      <el-tooltip
        class="item"
        effect="dark"
        content="点击进行更换"
        placement="right"
      >
        <el-image
          v-if="itemc[it.valueCode]"
          style="width: 99%; height: 99%"
          :src="itemc[it.valueCode]"
        ></el-image>
        <i v-else class="el-icon-plus avatar-uploader-icon"></i>
      </el-tooltip>
      <!-- <el-button size="small" type="primary">点击上传</el-button> -->

      <div slot="tip" class="el-upload__tip">
        只能上传jpg/png文件，且不超过500kb 
        <br>{{it.remark||''}}
        <el-progress
          :percentage="percentage"
          v-show="percentage !== 0"
          :status="
            percentage == 100
              ? 'success'
              : percentage >= 150
              ? 'exception'
              : 'null'
          "
        ></el-progress>
        <div v-show="percentage >= 150">上传失败请重试~</div>
      </div>
    </el-upload>
  </div>
</template>

<script>
import { action, baseURL } from "./../config";
export default {
  name: "uploadMy",
  data() {
    return {
      tiem: null,
      action,
      baseURL,
      percentage: 0,
      fileList: [],
    };
  },
  props: {
    itemc: {
      type: Object,
      default: () => {},
    },
    it: {
      type: Object,
      default: () => {},
    },
    indexc: {
      type: Number,
      default: -1,
    },
    childsCode: {
      type: String,
      default: "",
    },
  },
  methods: {
    // 上传时的钩子函数
    hadleProgress() {
      this.percentage=0
      this.tiem = setInterval(() => {
        this.percentage++;
      }, 500);
    },
    // 长传失败
    hadleError() {
      this.percentage = 150;
      clearInterval(this.tiem);
      setTimeout(() => {
        this.percentage = 0;
      }, 1000);
    },
    // 删除图片
    handleRemove() {
      console.log("indexc", this.indexc);
      this.$emit(
        "setJumpUrl",
        this.indexc,
        "",
        this.it.valueCode,
        this.childsCode
      );
    },
    // 删除
    handlePreview(file) {
      console.log(file);
    },
    handlesuccess(response) {
      if (!response?.data) {
        this.percentage = 150;
        clearInterval(this.tiem);
        setTimeout(() => {
          this.percentage = 0;
        }, 1500);
        return;
      }
      // 上传成功后
      setTimeout(() => {
        this.percentage = 0;
      }, 1000);
      console.log("indexc", this.indexc);
      clearInterval(this.tiem);
      this.percentage = 100;

      this.$emit(
        "setJumpUrl",
        this.indexc,
        response.data,
        this.it.valueCode,
        this.childsCode
      );
    },
    getHeaders() {
      let layuiAdmin = localStorage.getItem("layuiAdmin");
      // console.log("类有爱",layuiAdmin)
      let Authorization = layuiAdmin
        ? JSON.parse(layuiAdmin).Authorization
        : "";
      return {
        authorization: Authorization,
      };
    },
  },
};
</script>
<style lang="css">
</style>