<template>
  <div id="app">
    <index v-if="indexState"></index>
  </div>
</template>

<script>
import index from "./components/index";
// import management from "./components/configuration/management";
export default {
  name: "App",
  components: {
    index,
  },
  data() {
    return {
      indexState: false,
    };
  },

  methods: {},
  mounted() {},
  beforeCreate() {
    window.$message = this.$message;
    window.$notify = this.$notify;
    this.$api.mchMiniAuth().then((res) => {
      if (!res || !res.length) {
        this.$alert( '您暂未开通小程序','温习提示！~', {
          confirmButtonText: '确定',
          callback: () => {
            this.$message({
              type: 'info',
              message: `无小程序编辑信息`
            });
          }
        });
        return;
      }
      res = res.filter(item=>item.name&&item.authFrom)
      this.$store.dispatch("authFrom/setAuthFromDataActions", res);
      this.indexState = true;
    });
  },
};
</script>

<style>
::-webkit-scrollbar {
  /* background: #b3c0d1; */
  background: #dedede;
  width: 6px;
}
::-webkit-scrollbar-thumb {
  box-sizing: border-box;
  border-radius: 10px;
  -webkit-box-shadow: inset 0 0 5px rgba(0, 0, 0, 0.2);
  background: rgba(0, 0, 0, 0.2);
}
.item {
  width: 100px;
  height: 100px;
  border: solid 1px #ccc;
}
.flex {
  display: flex;
  justify-content: space-around;
}
</style>
