<template>
  <div id="main" class="mian-div" v-show="itemDataFun()" v-if="itemStat">
    <!-- <div id="main" class="mian-div"> -->
    <el-tabs type="border-card" v-model="editableTabsValue">
      <!-- 属性 -->
      <el-tab-pane
        :label="name + '属性'"
        v-if="!!setAttribute.length || !!setChildsAttribute.length"
        name="shuxing"
      >
        <div class="main_div main_div_r_items" id="items">
          <el-form ref="form" :model="form" label-width="80px">
            <template>
              <!-- 公共属性设置：
              setAttribute设置
              data里添加默认属性
              -->
              <el-form-item
                :label="it.name"
                v-for="(it, ind) in setAttribute"
                :key="ind + 1"
              >
                <template v-if="it.valueType == 'select'">
                  <el-select v-model="form[it.valueCode]" placeholder="请选择">
                    <el-option
                      :label="item.name"
                      :value="item.value"
                      v-for="(item, index) in it[it.valueCode]"
                      :key="index"
                    ></el-option>
                  </el-select>
                </template>
                <!-- 地址 -->
                <!--  <template v-if="it.valueType=='selects'">
                  <selects :itemc="itemc" :it="it" :indexc="indexc" @setJumpUrl="setJumpUrl"></selects>
                </template>-->
                <template v-if="it.valueType == 'input'">
                  <el-input v-model="form[it.valueCode]"></el-input>
                </template>
                <template v-if="it.valueType == 'colorPicker'">
                  <el-color-picker
                    v-model="form[it.valueCode]"
                    show-alpha
                  ></el-color-picker>
                </template>
                <template v-if="it.valueType == 'slider'">
                  <sliderMy
                    :itemc="form[it.valueCode]"
                    :it="it"
                    :indexc="ind"
                    @setJumpUrl="setSliderF"
                  ></sliderMy>
                </template>
                <!-- <template v-if="it.valueType=='slider'">
                  <div class="block">
                    <sliderMy v-model="form[it.valueCode]"> </sliderMy>
                  </div>
                </template >-->
                <template v-if="it.valueType == 'radioGroup'">
                  <el-radio-group v-model="form[it.valueCode]">
                    <el-radio
                      :label="item.value"
                      v-for="(item, index) in it[it.valueCode]"
                      :key="index"
                      >{{ item.name }}</el-radio
                    >
                  </el-radio-group>
                </template>

                <template v-if="it.valueType == 'upload'">
                  <uploadMy
                    :itemc="form"
                    :it="it"
                    @setJumpUrl="setUploadF"
                  ></uploadMy>
                  <!--   <el-upload
                    class="upload-demo"
                    :action="baseURL+action"
                    :headers="getHeaders()"
                    :on-preview="handlePreview"
                    :on-success="handlesuccess"
                    :on-remove="handleRemove"
                    :file-list="form[it.valueCode]"
                    :show-file-list="false"
                    list-type="picture-card"
                  > <el-tooltip class="item" effect="dark" content="点击进行更换" placement="right">
                    <el-image
                      v-if="form[it.valueCode]"
                      style="width: 99%; height: 99%"
                      :src="fileListFun(form[it.valueCode])"
                    ></el-image>
                    <i v-else class="el-icon-plus avatar-uploader-icon"></i>
                    </el-tooltip>
                    <div slot="tip" class="el-upload__tip">只能上传jpg/png文件，且不超过500kb</div>
                  </el-upload>-->
                </template>
              </el-form-item>
            </template>
          </el-form>
          <!-- 单纯的子组件 -->
          <template
            v-if="!!setChildsAttribute.length && !typeOfObj(childsForm)"
          >
            {{ name }}子组件设置：
            <el-form
              ref="form"
              :model="form"
              label-width="80px"
              v-for="(itemc, indexc) in childsForm"
              :key="indexc + 'index02'"
            >
              <el-form-item
                :label="it.name + (indexc + 1)"
                v-for="(it, ind) in setChildsAttribute"
                :key="ind"
              >
                <template v-if="it.valueType == 'select'">
                  <el-select v-model="itemc[it.valueCode]" placeholder="请选择">
                    <el-option
                      :label="item.name"
                      :value="item.value"
                      v-for="(item, index) in it[it.valueCode]"
                      :key="index + 'index02'"
                    ></el-option>
                  </el-select>
                </template>
                <!-- 地址 -->
                <template v-if="it.valueType == 'selects'">
                  <selects
                    :itemc="itemc"
                    :it="it"
                    :indexc="indexc"
                    @setJumpUrl="setJumpUrl"
                  ></selects>
                </template>
                <template v-if="it.valueType == 'input'">
                  <el-input v-model="itemc[it.valueCode]"></el-input>
                </template>
                <template v-if="it.valueType == 'colorPicker'">
                  <el-color-picker v-model="color" show-alpha></el-color-picker>
                </template>
                <template v-if="it.valueType == 'slider'">
                  <sliderMy
                    :itemc="itemc"
                    :it="it"
                    :indexc="indexc"
                    @setJumpUrl="setSlider"
                  ></sliderMy>
                </template>
                <template v-if="it.valueType == 'radioGroup'">
                  <el-radio-group v-model="itemc[it.valueCode]" size="small">
                    <el-radio-button
                      :label="item.name"
                      v-for="(item, index) in it[it.valueCode]"
                      :key="index + 'index02'"
                    ></el-radio-button>
                  </el-radio-group>
                </template>
                <template v-if="it.valueType == 'upload'">
                  <uploadMy
                    :itemc="itemc"
                    :it="it"
                    :indexc="indexc"
                    @setJumpUrl="setUpload"
                  ></uploadMy>
                </template>
              </el-form-item>
              <el-button
                icon="el-icon-delete"
                type="danger"
                circle
                @click="del(itemc, indexc)"
                class="button_css"
                v-if="delflage"
              ></el-button>
            </el-form>
            <div v-if="flages">
              添加{{ name }}子组件：
              <el-button
                type="primary"
                icon="el-icon-plus"
                circle
                @click="add(itemc)"
              ></el-button>
            </div>
          </template>

          <!-- <el-button type="success">保存</el-button>
          <el-button type="danger">删除</el-button>-->
        </div>
      </el-tab-pane>

      <!--  -->
      <el-tab-pane
        :label="name + '子组件属性'"
        v-if="!!setAttribute.length && typeOfObj(childsForm)"
        name="1"
      >
        <div class="main_div main_div_r_items" id="items">
          <!-- 多方嵌套的子组件 -->
          <template v-if="typeOfObj(childsForm)">
            <!-- {{name}}子组件属性设置： -->
            <el-form
              ref="form"
              :model="form"
              label-width="80px"
              v-for="(itemc, indexc) in setChildsAttribute"
              :key="indexc + 'index03'"
            >
              {{ itemc.childsName }}组件内设置：
              <div
                v-for="(it, ind) in childsForm[itemc.childsCode]"
                :key="ind + 'dsf'"
                class="childs_code_k"
              >
                序号{{ ind + 1 }}：
                <el-form-item
                  :label="itV.name"
                  v-for="(itV, indV) in itemc.childsValue"
                  :key="indV * 200 + itemc.childsName"
                >
                  <div>
                    <template v-if="itV.valueType == 'select'">
                      <el-select
                        v-model="it[itV.valueCode]"
                        placeholder="请选择"
                      >
                        <el-option
                          :label="item.name"
                          :value="item.value"
                          v-for="(item, index) in it[itV.valueCode]"
                          :key="index + 'index077'"
                        ></el-option>
                      </el-select>
                    </template>
                    <!-- 地址 -->
                    <template v-if="itV.valueType == 'selects'">
                      <selects
                        :itemc="it"
                        :it="itV"
                        :indexc="ind"
                        @setJumpUrl="setJumpUrlChild"
                        :childsCode="itemc.childsCode"
                      ></selects>
                    </template>
                    <template v-if="itV.valueType == 'input'">
                      <el-input v-model="it[itV.valueCode]"></el-input>
                    </template>
                    <template v-if="itV.valueType == 'colorPicker'">
                      <el-color-picker
                        v-model="color"
                        show-alpha
                      ></el-color-picker>
                    </template>
                    <template v-if="itV.valueType == 'slider'">
                      <sliderMy
                        :itemc="it"
                        :it="it"
                        :indexc="ind"
                        @setJumpUrl="setSliderChild"
                        :childsCode="itemc.childsCode"
                      ></sliderMy>
                    </template>
                    <template v-if="itV.valueType == 'radioGroup'">
                      <el-radio-group v-model="it[itV.valueCode]" size="small">
                        <el-radio-button
                          :label="item.name"
                          v-for="(item, index) in it[itV.valueCode]"
                          :key="index + 'index02'"
                        ></el-radio-button>
                      </el-radio-group>
                    </template>
                    <template v-if="itV.valueType == 'upload'">
                      <uploadMy
                        :itemc="it"
                        :it="itV"
                        :indexc="ind"
                        @setJumpUrl="setUploadChild"
                        :childsCode="itemc.childsCode"
                      ></uploadMy>
                    </template>
                  </div>
                </el-form-item>
                <!-- {{itemc.childsValue[ind].name}} -->
                <el-button
                  icon="el-icon-delete"
                  circle
                  @click="delChilds(it, ind, itemc.childsCode)"
                  class="button_css"
                ></el-button>
              </div>
              添加新{{ itemc.childsName }}：
              <el-button
                type="primary"
                icon="el-icon-plus"
                circle
                @click="addChilds(null, itemc.childsCode)"
              ></el-button>
            </el-form>
          </template>
        </div>
      </el-tab-pane>
      <!-- 样式 -->

      <el-tab-pane :label="name + '样式'" v-if="flage" name="2">
        <div class="main_div main_div_r_items" id="items">
          <el-form ref="form" :model="stylesObj" label-width="80px">
            <el-form-item
              :label="it.name"
              v-for="(it, ind) in stylesObjxx"
              :key="ind + 'index01'"
            >
              <template v-if="it.valueType == 'select'">
                <el-select
                  v-model="stylesObj[it.valueCode]"
                  placeholder="请选择"
                >
                  <el-option
                    :label="item.value"
                    :value="item.value"
                    v-for="(item, index) in it[it.valueCode]"
                    :key="index + 'index01'"
                    >{{ item.value }}</el-option
                  >
                </el-select>
              </template>
              <!-- 地址 -->
              <template v-if="it.valueType == 'selects'">
                <selects
                  :itemc="stylesObj[it.valueCode]"
                  :it="it"
                  :indexc="indexc"
                  @setJumpUrl="setJumpUrl"
                ></selects>
              </template>
              <template v-if="it.valueType == 'input'">
                <el-input v-model="stylesObj[it.valueCode]"></el-input>
              </template>
              <template v-if="it.valueType == 'colorPicker'">
                <el-color-picker
                  v-model="stylesObj[it.valueCode]"
                  show-alpha
                ></el-color-picker>
              </template>
              <template v-if="it.valueType == 'slider'">
                <sliderMy
                  :itemc="stylesObj[it.valueCode]"
                  :it="it"
                  :indexc="ind"
                  @setJumpUrl="setSliderSty"
                ></sliderMy>
              </template>
              <template v-if="it.valueType == 'radioGroup'">
                <el-radio-group v-model="stylesObj[it.valueCode]" size="small">
                  <el-radio-button
                    :label="item.name"
                    v-for="(item, index) in it[it.valueCode]"
                    :key="index + 'index01'"
                  ></el-radio-button>
                </el-radio-group>
              </template>
            </el-form-item>
          </el-form>
        </div>
      </el-tab-pane>

      <el-button
        v-show="indexData != '-1'"
        type="danger"
        @click="delcomp"
        class="button_danger_my"
        >删除{{ name }}</el-button
      >
    </el-tabs>
  </div>
</template>


<script>
// import { component } from 'vue/types/umd';
// import { component } from 'vue/types/umd';
import cpmpDataAttribute from "./configuration/cpmpDataAttribute";
// import cpmpDataAttributeC from "./configuration/cpmpDataAttributeC";
import selects from "./selects";
import uploadMy from "./uploadMy";
import sliderMy from "./sliderMy";
import { action, baseURL } from "./../config";
export default {
  name: "compRight",
  data() {
    return {
      itemStat: true,
      flageData: true,
      cpmpDataAttribute,
      action,
      baseURL,
      urlData: {}, //url数据
      loading: false,
      urlState: false,
      optionsUrl: [], //地址详情
      editableTabsValue: "shuxing",
      name: "",
      fileList: [
        // {
        //   name: "food.jpeg",
        //   url:
        //     "https://fuss10.elemecdn.com/3/63/4e7f3a15429bfda99bce42a18cdd1jpeg.jpeg?imageMogr2/thumbnail/360x360/format/webp/quality/100",
        // },
        // {
        //   name: "food2.jpeg",
        //   url:
        //     "https://fuss10.elemecdn.com/3/63/4e7f3a15429bfda99bce42a18cdd1jpeg.jpeg?imageMogr2/thumbnail/360x360/format/webp/quality/100",
        // },
      ],
      itemData: {},
      stylesObj: {},
      stylesObjxx: [
        {
          name: "底色",
          valueCode: "background",
          valueType: "colorPicker",
          childsType: false,
          background: "", //icon：“顶顶顶”
        },
        {
          name: "高度",
          valueCode: "height",
          valueType: "slider",
          childsType: false,
          remark: "0",
          height: "", //icon：“顶顶顶”
        },

        {
          name: "上边距",
          valueCode: "marginTop",
          valueType: "slider",
          childsType: false,
          remark: "0",
          marginTop: "", //icon：“顶顶顶”
        },
      ], //样式对象
      setAttribute: [],
      setChildsAttribute: [],
      indexData: null,
      listName: [
        {
          name: "样式",
        },
        {
          name: "数据",
        },
        // ,{ name: "标准组件" },
      ],
      form: {},
      childsForm: [],
      flage: "",
      flages: "",
      delflage: "",
    };
  },
  watch: {
    // 监听所选内容
    ["$store.state.type.itemData"](value) {
      this.editableTabsValue = "shuxing";
      this.itemStat = false;
      if (!value) {
        this.itemData = value;
        return;
      }
      let form = {};
      this.itemData = value;
      this.name = cpmpDataAttribute[value.key].name;
      this.stylesObj = value.stylesObj;
      // 筛选组件是否需要公共样式
      this.flage = cpmpDataAttribute[value.key].compStyle;
      this.flages = cpmpDataAttribute[value.key].add;
      this.delflage = cpmpDataAttribute[value.key].del;
      this.childsForm = value.childs || [];
      this.setAttribute = cpmpDataAttribute[value.key].setAttribute.filter(
        (item) => {
          // if (value.data[item.valueCode] !== undefined && !item.childsType) { // 保证data中拥有默认数据
          if (!item.childsType) {
            form[item.valueCode] = value.data[item.valueCode];
            return item;
          }
        }
      );
      this.setChildsAttribute = cpmpDataAttribute[
        value.key
      ].setAttribute.filter((item) => {
        //子组件属性
        if (item.childsType) {
          return item;
        }
      });
      this.form = form;
      setTimeout(() => {
        this.itemStat = true;
      });
    },
    // indexData--被选中组件索引监听
    ["$store.state.type.indexData"](value) {
      // 监听所选
      if (value == -1) {
        console.log("va", value);
      }
      this.indexData = value;
    },
    //监听更改小程序类型
    ["$store.state.authFrom.authFromInfo.authFrom"]() {
      this.itemData = {};
    },

    // 监听公共属性from更改
    form(value) {
      // 将新的值传递到componentList中
      //处理对象内容
      let itemData = this.itemData;
      let indexData = this.indexData;
      itemData.data = value;
      if (indexData == -1) {
        this.$store.dispatch("type/updataTabbarDataActions", {
          itemData,
        });
        return;
      }
      this.$store.dispatch("type/updataTypeDataActions", {
        indexData,
        itemData,
      });
    },
    // 监听公共样式更新
    stylesObj(value) {
      // 将新的值传递到componentList中
      let itemData = this.itemData;
      let indexData = this.indexData;

      itemData.stylesObj = value;
      // console.log(" itemData.stylesObj", itemData.stylesObj);
      if (indexData == -1) {
        this.$store.dispatch("type/updataTabbarDataActions", {
          itemData,
        });
        return;
      }
      this.$store.dispatch("type/updataTypeDataActions", {
        indexData: this.indexData,
        itemData,
      });
    },
    // 监听子组件表单数组更改
    childsForm(value) {
      // 将新的值传递到componentList中
      let itemData = this.itemData;
      let indexData = this.indexData;

      itemData.childs = value;
      if (indexData == -1) {
        this.$store.dispatch("type/updataTabbarDataActions", {
          itemData,
        });
        return;
      }
      this.$store.dispatch("type/updataTypeDataActions", {
        indexData: this.indexData,
        itemData,
      });
    },
  },
  methods: {
    // 出现右侧显示
    itemDataFun() {
      return JSON.stringify(this.itemData) != "{}";
    },
    setJumpUrl(indexc, itemc) {
      //更改跳转地址
      this.childsForm[indexc].urlState = itemc.urlState;
      this.childsForm[indexc].jumpUrl = itemc.jumpUrl;
      this.childsForm[indexc].nameVal = itemc.nameVal;
      // this.childsForm[indexc].urlState = itemc
    },
    setJumpUrlChild(indexc, itemc, childsCode) {
      //更改跳转地址
      this.childsForm[childsCode][indexc].urlState = itemc.urlState;
      this.childsForm[childsCode][indexc].jumpUrl = itemc.jumpUrl;
      this.childsForm[childsCode][indexc].nameVal = itemc.nameVal;
      // this.childsForm[indexc].urlState = itemc
    },
    setUpload(indexc, itemc, valueCode) {
      //更改上传返回的地址; 图片地址
      this.childsForm[indexc][valueCode] = itemc;
    },
    setUploadF(indexc, itemc, valueCode) {
      //更改上传返回的地址; 图片地址
      indexc || "";
      this.form[valueCode] = itemc;
    },
    setUploadChild(indexc, itemc, valueCode, childsCode) {
      //更改上传返回的地址；图片地址
      this.childsForm[childsCode][indexc][valueCode] = itemc;
    },
    fileListFun(val) {
      return val;
    },
    setSlider(indexc, itemc, valueCode) {
      //更改滑块值
      this.childsForm[indexc][valueCode] = itemc;
    },
    setSliderSty(indexc, itemc, valueCode) {
      //更改滑块值
      indexc || "";
      let stylesObj = JSON.parse(JSON.stringify(this.stylesObj));
      stylesObj[valueCode] = itemc;
      this.stylesObj = stylesObj;
    },
    setSliderF(indexc, itemc, valueCode) {
      //更改滑块值
      "" || indexc;
      this.form[valueCode] = itemc;
    },
    setSliderChild(indexc, itemc, valueCode, childsCode) {
      //更改滑块值
      this.childsForm[childsCode][indexc][valueCode] = itemc;
    },
    // 增加
    addChilds(item, name) {
      // console.log(this.childsForm[name]);
      this.childsForm[name].push(
        JSON.parse(JSON.stringify(this.childsForm[name][0]))
      );
      this.$notify({
        title: "成功",
        message: "成功添加了",
        type: "success",
        position: "top-left",
      });
    },
    // 删减
    delChilds(item, index, name) {
      if (this.childsForm[name].length == 1) {
        this.$message({
          message: "不可在删！！",
          type: "warning",
        });
        return;
      }
       this.$confirm('此操作为删除, 是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
           this.childsForm[name].splice(index, 1);
          this.$message({
            type: 'success',
            message: '删除成功!'
          });
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '已取消删除'
          });          
        });
     
    },
    // 删除整个组件
    delcomp() {
      this.$confirm("此操作为删除操作, 是否继续?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning",
      })
        .then(() => {
          this.$store.dispatch("type/delTypeDataActions", this.indexData);
          this.setAttribute = [];
          this.flage = false;
          this.$message({
            type: "success",
            message: "删除成功!",
          });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "已取消删除",
          });
        });
    },
    // 添加子组件
    add() {
      this.childsForm.push(JSON.parse(JSON.stringify(this.itemData.childs[0])));
      this.$notify({
        title: "成功",
        message: "成功添加了子组件",
        type: "success",
        position: "top-left",
      });
    },
    // 删除子组件
    del(item, index) {
      if (this.childsForm.length == 1) {
        this.$message({
          message: "不可在删！！",
          type: "warning",
        });
        return;
      }
       this.$confirm('此操作为删除操作, 是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
           this.childsForm.splice(index, 1);
          this.$message({
            type: 'success',
            message: '删除成功!'
          });
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '已取消删除'
          });          
        });
     
    },
    // 暂时无用
    onSubmit() {
      alert("submit!");
    },
    // 上传图片
    handleRemove(file, fileList) {
      console.log(file, fileList);
    },
    // 删除图片
    handlePreview(file) {
      console.log(file);
    },
    handlesuccess(response, file, fileList) {
      // 上传成功后
      console.log("handlesuccess", response, file, fileList);
    },
    change(ev, val) {
      console.log(ev);
      this.stylesObj[val] = ev + "px";
    },
    typeOfObj(childsForm) {
      return Object.prototype.toString.call(childsForm) === "[object Object]";
    },
    getHeaders() {
      let layuiAdmin = localStorage.getItem("layuiAdmin");
      let Authorization = JSON.parse(layuiAdmin).Authorization || "";
      return {
        authorization: Authorization,
      };
    },
  },
